}
.table__cost td {
  padding-bottom: 15px;
}
.table__advantages {
  font-size: 10px;
  font-family: "Arial", sans-serif;
  border: none;
}
.table__advantages td {
  padding-bottom: 10px;
}
.table__button td {
  padding-top: 10px;
}
.table__button button {
  border: none;
  display: inline-block;
  text-transform: uppercase;
  background-color: #ffe26d;
  color: #000000;
  padding: 17px 23px;
  width: auto;
  font-size: 10px;
  font-weight: 700;
  line-height: 10px;
  border-radius: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.table__button button:hover {
  background-color: #62497c;
  color: #ffffff;
}
.video {
  background-color: #62497c;
  color: #ffffff;
  padding-top: 85px;
  padding-bottom: 115px;
}
.video__header {
  font-size: 44px;
  line-height: 40px;
  text-align: center;
  margin: 0;
}
.video__block {
  min-height: 495px;
}
.team {
